.login-page {
  background-color:#dcdfeeb6;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: rgba(33, 32, 32, 0.35) 0px 5px 15px;
}
.login-page h1 {
  text-align: center;
  font-weight: bold;
  margin: 30px;
  color: #344265;
}

.login-form {
  background-color:#c3b29664;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding: 20px;
  width: 31%;
  height: 60%;
  margin-left: 35%;
}

.login-form h1 {
  text-align: center;
  font-weight: bold;
  margin: 30px;
  color: #344265;
}

.login-form .ant-form-item-label label {
  color: #344265 !important;
}
.login-form button {
  background-color: #344265;
  color: white;
  outline: none;
}
.login-form button:hover {
  background-color: black;
  color: white;
}
.login-form a {
  color: #344265 !important;
}

.login-form a:hover {
  color: black !important;
}