* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.content {
  height: 80vh;
}

.register-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px rgb(60, 50, 50);
  margin-bottom: 12px;
  background-color: rgb(207, 209, 202);
}

.table {
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px rgb(60, 50, 50);
  background-color:  rgb(207, 209, 202);
}